*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.menu_msg{
    width: 100%;
    height: 4px;
    background-color: #8e8e8e00;
}

.container_msg2{
    width: 100%;
    height: 100vh;
    display: flex;
    padding: 20px 0 0 0;
    background: #8e8e8e00;
    position: relative;
}

.container2{
    position: absolute;
    left: 43%;
    height: 649px;
    width: 583px;
    background: white;
    border: 1px solid #8e8e8e45;
    
}

.container_msg{
    position: absolute;
    left: 20.1%;
    height: 649px;
    width: 349px;
    background: white;
    border: 1px solid #8e8e8e45;
    border-top: none;
    border-radius: 4px 0 0 4px;
    overflow-y: scroll;
}

.chat_msg{
    display: flex;
    align-items: center;
    border-bottom: 1px solid #8e8e8e45;
    border-top: 1px solid #8e8e8e45;
    width: 100%;
    height: 60px;
    background-color: white;
    border-radius: 4px 0 0 0;
    font-weight: 500;
}
.chat_msg p:nth-child(1),.chat_msg p:nth-child(3) {
    padding: 4px 1px 1px 80px;
}
.chat_msg p:nth-child(2){
    margin-top: 6px;
    padding: 0 4px 0 10px;
    
}

.msg{
    display: flex;
    align-items: center;
}

.msg_img{
    padding: 20px 0 0 20px;
}

.msg img{
    border-radius: 50%;
    padding: 0 0 0 0;
    object-fit: cover;
}

.user_perfil{
    height: 60px;
    font-size: 14px;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 20px;
}

.user_perfil p:nth-child(1){
    color: #262626;
}
.user_perfil p:nth-child(2){
    color: #8e8e8e;
}

.container2{
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-top: 14%;
    border-radius: 0 4px 4px 0;
}
.container2 button{
    width: 132.14px;
    font-size: 14px;
    height: 30px;
    border-radius: 4px;
    border: none;
    background: #0095f6;
    color: white;
    
}

.container2 h2{
    font-size: 20px;
    font-weight: 200;
}
.container2 p{
    position: relative;
    font-size: 15px;
    font-weight: 100;
    color: #8e8e8e;
    padding: 10px 0 25px 10px;
   
}

.container2 img{
    -moz-transform: scaleX(-1);
    -o-transform: scaleX(-1);
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
}
