.wrapper__profile{

    position:relative;
    width:1100px;
    left:50%;
    transform:translateX(-50%);
}

.wrapper__contentProfile{
    display: flex;
    position:relative;
    justify-content:center;
    align-items: center;
   
}
.container__profileContent{
    display:flex;
    flex-direction:column;
    row-gap:26px;
    padding: 40px;
    margin-right: 47px;
}
.wrapper__profileFirst,
.wrapper__profileSecond,
.wrapper__profileThird{
    display:flex;
    align-items:center;
}

.wrapper__profileFirst>.name{
    font-weight: 200;
}

.wrapper__profileFirst>.editar{
    font-size: 14.49px;
    font-weight: 600;
    margin: 0 10px 4px 10px;
    padding: 5px 9px;
    border-radius: 3px;
    border: 1px solid #2626264f
}
/*Borda = pixel da borda, estilo da borda e cor 
border: 1px solid black
se quiser, pode colocar bordas separadas:
border-top: 2px, solid black
border-bottom: 2px, solid black*/

.wrapper__profileSecond{
    width: 317px;
    justify-content: space-between;
}
.wrapper__profileThird>ul{
    list-style-type:none;
}

.wrapper__profileImg{
    width: 150px;
    height: 150px;
    position:absolute;
    left:15%;
    top: 10%;
}

.wrapper__profileImg>img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
}

.configuracao{
    width: 24px;
    height: 24px;
    margin: 0 0 3px 5px;
    cursor: pointer;
}

.configuracao>img{
    width: 100%;
    height: 100%;
}

.destaque{
    justify-content: center;
    align-items: center;
    margin-left: -180px;
    
    
}

.foto>img{
border-radius: 50%;
object-fit: cover;
border: 2px solid rgba(105, 105, 105, 0.447);
margin: 10px 23px;
padding: 2px;
cursor: pointer;
}

.linha{
    margin-top: 4rem;
    margin-left: auto;
    margin-right: auto;
    border: 1px solid rgba(161, 158, 158, 0.251);
}


.linha2{
    width: 110px;
    border: 1px solid rgba(11, 11, 11, 0.575);
    margin: -2px 350px 0px; 
}

 .divPublicacao2{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}
.divPublicacao2 a{
    font-size: 12px;
    letter-spacing: 1px;
    margin: 0px 25px 0px 18px;
    text-decoration: none;
    color: #8e8e8e;
}

.divPublicacao2 a:nth-child(2){
    color: #262626;
}

.identificacoes2{
    display: flex;
    align-items: center;
    padding: 10px 350px; 
    cursor: pointer;
}

.postagem{
    display: flex;
    align-items: center;
    margin-left: 95px;
    margin-top: 2px;
}

.photo>img{
    object-fit: cover;
    margin: 0px 23px 570px 10px;
    padding: 8px;
    cursor: pointer;
}





