.app__header {

  position: sticky;

  top: 0;

  z-index: 9999;

  background-color: rgb(255, 255, 255);

  height: 54px;

  display: flex;

  align-items: center;

  object-fit: contain;

  border-bottom: 1px solid rgb(211, 211, 211);


}

.app__headerWrapper {

  max-width: 950px;

  margin: 0 auto;

  width: 100%;

  padding: 0 20px;

  display: flex;

  align-items: center;

  justify-content: space-between;

}

.app__headerWrapper img {
  margin-bottom: -7px;

}

.pesquisar {
  display: flex;
  align-items: center;

}

.pesquisar input {
  margin: 0px;
  margin-left: 170px;
  padding: 5px 37px;
  width: 280px;
  height: 40px;
  border-radius: 8px;
  border: none;
  background-color: rgba(214, 211, 211, 0.211);


}

.pesquisar input:focus {
  outline: none;
}


.pesquisar img {
  padding-bottom: 8px;
  margin-left: 185px;
  position: absolute;

}

.app__headerButtons {

  display: flex;

  column-gap: 6px;





}

.app__headerButtons button {
  margin-right: 10px;
}

button {

  font-size: 4px;

