*{
    padding:0;
    margin:0;
    box-sizing:border-box;
}

.container_explore{
    width:100%;
    height:100%;
    background: color #8e8e8e;;
    display:flex;
    align-items:center;
    justify-content:center;
    padding:20px;
    position:relative;
    left:50%;
    top:0px;
    transform:translateX(-50%)
}

.wrapper__explore{
    width:80%;
    display:flex;
    flex-wrap:wrap;
    row-gap:10px;
    justify-content:center;
    margin-top:10px;
}

.primeiro_conteudo{
    display:flex;
    justify-content:center;
    width:943px;
    height:613px;
    margin: 0px 0px 0px 450px;
}
.segundo_conteudo{
    display:flex;
    width:943px;
    height:613px;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    row-gap:20px;
    padding:20px;
    margin: 0px 0px 0px 295px;
}
.terceiro_conteudo{
    display:flex;
    width:943px;
    height:613px;
    justify-content:center;
    column-gap:23px;
}

.wrapper__firstline,
.wrapper__secondline{
    display:flex;
    justify-content:space-between;;
    column-gap:25px;
}

.wrapper__img{
    display:flex;
    flex-direction:column;
}

.meme1{
object-fit: cover;
margin: 10px 0px 0px -440px;

}

.meme2{

    margin: 10px 0px 0px -440px;

}

.video1>img{
    margin: 10px 0px 0px -110px;
}

.recife{
    margin: 0px 0px 199px -115px;
}

.recife_antigo{
    margin: 0px 0px 199px 2px;
}

.small__video{
    position:relative;
    width:300px;
    height:300px;
    left: 50px;
    cursor:pointer;
}





