.story {
    background-color: #fff;
    border: 1px solid rgba(175, 173, 173, 0.514);
    border-radius: 3px;
    padding: 5px;
    width: 650px;
    height: 100px;
    display: flex;
    justify-content: space-between;    
    position: absolute; 
    left: 20%; 
}

.itemStory {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 0px 3px;
    padding: 2px;
}

.itemStory div {
    padding: 3px;
    background: linear-gradient(to top, #c33764, #1d2671);
    border-radius: 50%;
    line-height: 0;
}

.itemStory p{
    margin-top: 6px;
    font-size: 11px;
}

.itemStory img{
    border-radius: 50%;
    object-fit: cover;
}


