.wrapper__profile{
    position:relative;
    width:1100px;
    left:50%;
    transform:translateX(-50%);
}

.wrapper__contentProfile{
    display: flex;
    position:relative;
    justify-content:center;
    align-items: center;
   
}
.container__profileContent{
    display:flex;
    flex-direction:column;
    row-gap:26px;
    padding: 40px;
    margin-right: 47px;
}
.wrapper__profileFirst,
.wrapper__profileSecond,
.wrapper__profileThird{
    display:flex;
    align-items:center;
}

.wrapper__profileFirst>.name{
    font-weight: 200;
}

.wrapper__profileFirst>.editar{
    font-size: 14.49px;
    font-weight: 600;
    margin: 0 10px 4px 10px;
    padding: 5px 9px;
    border-radius: 3px;
    border: 1px solid #2626264f
}
/*Borda = pixel da borda, estilo da borda e cor 
border: 1px solid black
se quiser, pode colocar bordas separadas:
border-top: 2px, solid black
border-bottom: 2px, solid black*/

.wrapper__profileSecond{
    width: 317px;
    justify-content: space-between;
}
.wrapper__profileThird>ul{
    list-style-type:none;
}

.wrapper__profileImg{
    width: 150px;
    height: 150px;
    position:absolute;
    left:15%;
    top: 10%;
}

.wrapper__profileImg>img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
}

