
.post{
    width: 650px;
    border: 1px solid rgba(175, 173, 173, 0.507);
    border-radius: 5px;
    padding-bottom: 10px;
    margin: 20px 0px;
    position: relative;
    left: -8%;
    top: 3.5%;
}

.postheader{
    display: flex;
    align-items: center;
    background-color: white;
    padding: 10px;
    justify-content: space-between;
}

.ponto{
    padding: 12px;
    margin: -4px 0px 0px 260px;
}

.container3{
    display: flex;
    align-items: center;
}

.container3 h3{
    margin: 10px;
}

.container3 div{
    width: 40px;
    height: 40px;
    border-radius: 50%;    
}

.container3 div>img{
    width: 100%;
    height: 100%;
    object-fit: cover; 
    border-radius: 50%;
    
 }

.postimage{
    width: 100%;
}

.icone{
    padding: 8px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    
}

.container4{
    flex-direction: row;
    display: flex;
}
.icone svg{
    margin: 5px;
    
}

.icone img{
    object-fit: cover;
    margin: 5px 5px;
    width: 15px;
    height: 15px;

}
.balao{ 
    margin-bottom: 1px;
    -moz-transform: scaleX(-1);
    -o-transform: scaleX(-1);
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
}
.curtidas {
    display: flex;
    position: relative;
    width: 100%;
    padding: 0 0 0 12px;
}
.curtidas>h1{
    font-size: 14px;
    font-weight: 100;
}
.curtidas>p{
    margin: 0 0 0 5px;
    font-size: 13px;
}




.post__text{
    padding: 5px 12px;
    font-weight: 400;
    
}

.comentarios{ 
    padding: 0px 12px;
    font-weight: 300;
}

.comentario{
  padding: 2px;
  color: #8e8e8e;
  font-weight: 400;
}

.tempo{
    font-size: 10px;
    color: #8e8e8e;
    margin-left: 4px;
    
}

.comentar{
    border: none;
    border-top: 1px solid rgb(211, 211, 211);
    width: 100%;
    padding: 06px 19px;
    margin-top: 10px;
    align-items: center;
    display: flex;
    justify-content: space-between;
}

.comentar input{
    
    border: none;
    padding: 10px;
    padding-bottom: 5px;
    align-items: center;
}

.comentar input:focus{
    outline: none;
}

.comentar img{
    margin-top: 5px;
    margin-bottom: -3px;
    padding-bottom: -10px;
    align-items: center;
}

.Publicar{
    margin-left: 180px;
    margin-top: 5px;
    color: #0094f6;
    font-size: 15px;
    font-weight: bold;
   
}
.heart_coracao_red{
 position: relative;
bottom:3px;
background: none;
}
.heart__pub{
    position: relative;
    background: none;

}
.Publicar:disabled{
    color: #0094f64e;
    cursor: default;
}
.invisivel{
    display:none;
} 
.vermelho{
    color: red;
    opacity: 0.7;
    animation: coracao_vermelho 0.4s ease-in-out;
    width: 18px;
    height: 18px;
}
@keyframes coracao_vermelho{
    0%{
        transform:scale(0)
    }
    
    100%{
        transform:scale(1.2)
    }
}
    
    






